.container__result {
  display: flex;
  padding: 60px 0;
}

.container__into {
  display: flex;
  width: 80%;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
  height: 450px;
  margin: 0 auto;
  padding: 2px 0;

  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.list {
  padding: 15px 20px;
  overflow: auto;
}

.list::-webkit-scrollbar {
  width: 3px;
  background-color: #fff;
}

.list::-webkit-scrollbar-thumb {
  background-color: rgb(236, 65, 59);
}

.title {
  font-size: 22px;
  line-height: 1.3;
  font-weight: 700;
  color: rgb(236, 65, 59);
}

.start__container {
  width: 100%;
  text-align: center;
  margin-top: 120px;
  padding: 0 20px;
}

.button {
  height: 40px;
  display: block;
  background-color: rgb(236, 65, 59);
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  padding: 10px 20px;
  border-radius: 4px;
  border: 1px solid rgb(236, 65, 59);
  margin: 0 auto;
  cursor: pointer;

  transition-duration: 500ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.button:hover,
.button:active {
  background-color: rgb(241, 30, 23);
}

.button:disabled,
.button:disabled:hover {
  background-color: rgb(236, 65, 59, 30%);
  border: none;
  cursor: auto;
}

@media screen and (min-width: 768px) {
  .container__into {
    width: 600px;
  }

  .list {
    padding: 15px;
  }
}

@media screen and (min-width: 1200px) {
  .container__result {
    padding: 80px 0 100px;
  }

  .container__into {
    width: 700px;
  }
}
